img {
	width: 250px;
	height: auto;
}

.movie-container{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.clear{
	width: 6em;
}

.show{
	width: 10em;
}

.currentMovie{
	box-sizing: border-box;
	width: 600px;
	display: flex;
	flex-direction: column;
	background-color: #FFA9FB;
	text-align: left;
	border: 1em solid #D194F9;
	padding: 1em;
}

.preview{
	box-sizing: border-box;
	width: 600px;
	display: flex;
	flex-direction: column;
	background-color:#2D0055;
	color:#F0F0F0;
	border: 1em solid #A100A6;
	align-items: center;
	padding: 2em;
	text-align: center;
}

.moreDetails{
	padding-top: 1em;
}

div > p{
	font-weight: bold;
}
